<template>
<div class="app-page page-content-bottom-menu">
  <page-content class="page-content">
    <van-row class="row-1" type="flex" align="center">
        <title-second title="商品信息" :hideRight="true"></title-second>
    </van-row>
    <van-row class="row-2">
      <van-checkbox-group v-model="checked">
        <van-swipe-cell v-for="(item, index) in list" :key="index" :right-width="72">
          <van-cell-group>
            <van-row type="flex" align="center" class="row-2-1">
              <van-col class="row-2-1-col-1">
                <van-checkbox :key="item.id" :name="item.id" checked-color="#22ac38"></van-checkbox>
              </van-col>
              <van-col class="row-2-1-col-2">
                <img class="row-2-1-col-2-img" src="/static/images/shopping-cart/spxx.png"/>
              </van-col>
              <van-col class="row-2-1-col-3">
                <div class="row-2-1-col-3-div-1">新鲜茄子</div>
                <div class="row-2-1-col-3-div-2">新鲜茄子5kg</div>
                <div class="row-2-1-col-3-div-3">¥&nbsp;{{(item.price * item.quantity).toFixed(2)}}
                  <div @click="plusPrice(item)" class="row-2-1-col-3-div-3-div-1 row-2-1-col-3-div-3-div-1-button2 " style="color: #fff;">+</div>
                  <div class="row-2-1-col-3-div-3-div-1">{{item.quantity}}</div>
                  <div @click="minusPrice(item)" class="row-2-1-col-3-div-3-div-1 row-2-1-col-3-div-3-div-1-button1" >-</div>
                </div>
              </van-col>
            </van-row>
          </van-cell-group>
          <van-row class="row-2-right" type="flex" justify="center" align="center" slot="right"><div @click="remove(index)" class="row-2-right-div">删除</div></van-row>
        </van-swipe-cell>
      </van-checkbox-group>
    </van-row>
    <van-row class="row-3">
      <van-col class="row-3-col-1">
        <van-row class="row-3-col-1-row" type="flex" align="center">
          <van-col class="row-3-col-1-left"><div @click="checkAll"><van-checkbox v-model="checkall" checked-color="#22ac38"></van-checkbox></div></van-col>
          <van-col class="row-3-col-1-left2">全选</van-col>
          <van-col class="row-3-col-1-right">
            <van-row class="row-3-col-1-right-row-1">共计：<span class="row-3-col-1-right-row-1-span">{{totalMoney}}</span>元</van-row>
            <van-row class="row-3-col-1-right-row-2">配送费：1111元</van-row>
          </van-col>
        </van-row>
      </van-col>
      <van-col class="row-3-col-2">
        <div @click="goMakesureorder" class="row-3-col-2-div">去结算</div>
      </van-col>
    </van-row>
  </page-content>
</div>

</template>
<script>
import PageContent from '@/components/page/content'
import TitleSecond from '@/components/page/title'
export default {
  name: 'shoppingCart',
  components: {
    PageContent,
    TitleSecond
  },
  data () {
    return {
      // data: [
      //   {
      //     id: 1,
      //     money: 27.80,
      //     quantity: 1
      //   },
      //   {
      //     id: 2,
      //     money: 27.80,
      //     quantity: 1
      //   },
      //   {
      //     id: 3,
      //     money: 27.80,
      //     quantity: 1
      //   }
      // ],
      list: [{id: 1, price: 27.80, quantity: 1}, {id: 2, price: 27.80, quantity: 2}, {id: 3, price: 27.80, quantity: 3}],
      checked: [1, 2],
      checkall: false
    }
  },
  computed: {
    totalMoney () {
      let _this = this
      let Money = 0
      _this.checked.forEach(function (item1) {
        _this.list.forEach(function (item2) {
          if (item1 === item2.id) {
            Money += item2.price * item2.quantity
          }
        })
      })
      return Money.toFixed(2)
    }
  },
  methods: {
    goMakesureorder () {
      var id = 1
      this.$router.push({path: '/order/makesureorder', query: {id: id}})
    },
    remove (index) {
      this.list.splice(index, 1)
    },
    plusPrice (data) {
      data.quantity += 1
    },
    minusPrice (data) {
      if (data.quantity !== 1) {
        data.quantity -= 1
      }
    },
    checkAll () {
      let _this = this
      if (this.checkall) {
        // 实现全选
        this.checked = []
        this.list.forEach(function (item) {
          _this.checked.push(item.id)
        })
        console.log(this.checked)
      } else {
        this.checked = []
      }
    }
  },
  watch: {
    checked (value) {
      if (value.length === this.list.length) {
        this.checkall = true
      } else {
        this.checkall = false
      }
      // this.totalprice = 0
      // this.totalqty = 0
      // for (var i = 0; i < this.checked.length; i++) {
      //   for (var j = 0; j < this.shopCartList.length; j++) {
      //     if (this.checked[i] === this.shopCartList[j].id) {
      //       this.totalprice += this.shopCartList[j].price * this.shopCartList[j].qty
      //       this.totalqty += this.shopCartList[j].qty
      //     }
      //   }
      // }
    }
  },
  created () {
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .row-3{
    position:absolute;
    bottom: 0;
    height: 90px;
    width: 100%;
    .row-3-col-1{
      width: 513px;
      height: 90px;
      background-color: $whitef7f7f7;
      .row-3-col-1-row{
        height: 90px;
        .row-3-col-1-left{
          margin-left: 30px;
          width: 40px;
        }
        .row-3-col-1-left2{
          width: 60px;
          margin-left: 10px;
          font-family: $font-family-Medium;
          font-size: 28px;
          font-weight: normal;
          font-stretch: normal;
          color: $black999999;
        }
        .row-3-col-1-right{
          /*margin-right: 25px;*/
          /*width: calc(100% - 159px);*/
          display: flex;
          flex-direction: column;
          margin-left: 150px;
          font-family: $font-family-Medium;
          font-size: 28px;
          font-weight: normal;
          font-stretch: normal;
          color: $black666666;
          .row-3-col-1-right-row-1{
            margin-bottom: 10px;
            /*float: right;*/
            clear: both;
            margin-top: 5px;
            .row-3-col-1-right-row-1-span{
              font-family: $font-family-Medium;
              font-size: 30px;
              font-weight: normal;
              font-stretch: normal;
              color: $redc8251d;
            }
          }
          .row-3-col-1-right-row-2{
            clear: both;
            /*float: right;*/
            /*margin-right: 28px;*/
            font-family: $font-family-Medium;
            font-size: 20px;
            font-weight: normal;
            font-stretch: normal;
            color: $black999999;
          }
        }
      }
    }
    .row-3-col-2{
      width: 237px;
      height: 90px;
      background-color: $green22ac38;
      .row-3-col-2-div{
        line-height: 90px;
        text-align: center;
        height: 29px;
        font-family: $font-family-Medium;
        font-size: 30px;
        font-weight: normal;
        font-stretch: normal;
        color: $whiteffffff;
      }
    }
  }
  .row-2{
    width: 100%;
    overflow: auto;
    height: calc(100% - 161px);
    .row-2-1{
      .row-2-1-col-1{
        width: 116px;
        /deep/ .van-checkbox__icon{
          margin-left: 30px;
        }
      }
      .row-2-1-col-2{
        height: 100%;
        .row-2-1-col-2-img{
          height: 150px;
          width: 150px;
        }
      }
      .row-2-1-col-3{
        width: calc(100% - 266px);
        padding-left: 30px;
        padding-top: 30px;
        .row-2-1-col-3-div-1{
          margin-bottom: 20px;
          height: 29px;
          font-family: $font-family-Medium;
          font-size: 30px;
          font-weight: normal;
          font-stretch: normal;
          line-height: 34px;
          color: $black333333;
        }
        .row-2-1-col-3-div-2{
          margin: 10px 0;
          height: 22px;
          font-family: $font-family-Medium;
          font-size: 20px;
          font-weight: normal;
          font-stretch: normal;
          line-height: 34px;
          color: $black999999;
        }
        .row-2-1-col-3-div-3{
          margin: 19px 0 38px;
          height: 40px;
          font-family:  $font-family-Medium;
          font-size: 30px;
          font-weight: normal;
          font-stretch: normal;
          line-height: 40px;
          color: $redc8251d;
          .row-2-1-col-3-div-3-div-1-button1{
            font-family: $font-family-Regular;
            font-size: 30px!important;
            font-weight: normal;
            font-stretch: normal;
            line-height: 32px;
            color: $green40672b;
            border-radius: 20px;
            text-align: center;
            border: solid 1px $green40672b;
            width: 40px;
            height: 40px;
          }
          .row-2-1-col-3-div-3-div-1-button2{
            font-family: ﻿$font-family-Regular;
            font-size: 30px!important;
            font-weight: normal;
            font-stretch: normal;
            line-height: 32px;
            color: #ffffff;
            border-radius: 20px;
            text-align: center;
            background-color: $green22ac38;
            width: 40px;
            height: 40px;
          }
          .row-2-1-col-3-div-3-div-1{
            font-family: ﻿$font-family-Regular;
            font-size: 24px;
            font-weight: normal;
            font-stretch: normal;
            line-height: 34px;
            color: $black666666;
            margin-right: 30px;
            float: right;
            display: inline-block;
          }
        }
      }
    }
    .row-2-right{
      width: 143px;
      height: 100%;
      background-image:

      linear-gradient(
          $redc8251d,
          $redc8251d);
      background-blend-mode: normal,
      normal;
      .row-2-right-div{
        font-family: $font-family-Medium;
        font-size: 28px;
        font-weight: normal;
        font-stretch: normal;
        color: $whiteffffff;
      }
    }
  }
  .row-1{
    width: 100%;
    height: 71px;
    padding-left: 30px;
  }
</style>



